import React, { Component, Fragment } from 'react';
import { PieChart, Pie, Cell } from 'recharts';
import { numberToMoney, roundUpDouble } from 'utils/formatUtil';
import { makeid } from 'utils/functionUtil';
import { getChartAmountByEnrollReceivable } from 'services/chartFinance';
import { Tag } from 'antd';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent }) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <Fragment>
      {percent !== 0 ? (
        <text
          x={x}
          y={y}
          fill="white"
          textAnchor={x > cx ? 'start' : 'end'}
          dominantBaseline="central"
        >
          {`${roundUpDouble(percent * 100, 1)}%`}
        </text>
      ) : null}
    </Fragment>
  );
};

export default class AmountByEnrollReceivable extends Component {
  state = {
    students: [],
    widthChart: 500,
  };

  componentDidMount = async () => {
    const widthChart = document.getElementsByClassName('utils__title')[0].offsetWidth-30;
    this.setState({
      widthChart,
    });
    const res = await getChartAmountByEnrollReceivable();
    this.setState({
      students: res.data.pies.map(item => {
        item.name = item.label;
        return item;
      }),
    });
  };

  render() {
    const { students, widthChart } = this.state;
    return (
      <Fragment>
        { students && 
          <div>
            <PieChart width={widthChart} height={300}>
              <Pie
                data={students}
                cx={250}
                cy={150}
                labelLine={false}
                label={renderCustomizedLabel}
                outerRadius={100}
                fill="#8884d8"
                dataKey="value"
              >
                {students.map((entry, index) => (
                  <Cell key={`cell-${makeid(8)}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Pie>
            </PieChart>
            <div style={{ textAlign: 'center' }}>
              {students.map((entry, index) => (
                <Tag key={`cell-${makeid(8)}`} color={COLORS[index % COLORS.length]}>
                  {entry.name} : {numberToMoney(entry.value)}
                </Tag>
              ))}
            </div>
          </div>
        }
      </Fragment>
    );
  }
}



